.header {
    width: 100%;
    margin: 0;
    padding: 10px 0;
    background-color: #061A2E;
    border-style: solid;
    border-width: 1px;
    border-color: #2D374A;
  }
  
  .headertext {
    margin: 0;
    text-align: center;
    color: white;
  }

.container{
    width: 550px;
    height: 630px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 50px;
    border-radius: 20px;
    background-image: linear-gradient(to right, #3081ED, #00b0ff);
}

.top-bar{
    display: flex;
    justify-content: center;
    gap: 14px;
    padding-top: 45px;
}

.top-bar input{
    display: flex;
    width: 300px;
    height: 64px;
    background: #ebfffc;
    border: none;
    outline: none;
    border-radius: 40px;
    padding-left: 40px;
    color: #626262;
    font-size: 20px;
    font-weight: 400;
}

.search-icon{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 64px;
    height: 64px;
    background: #ebfffc;
    border-radius: 40px;
    cursor: pointer;
}

.weather-image {
    margin-top: 29px;
  }

.weather-temp {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 60px;
    font-weight: 400;
}

.weather-location {
    display: flex;
    justify-content: center;
    color: white;
    font-size: 40px;
    font-weight: 400;
}

.data-container {
    margin-top: 50px;
    color: white;
    display: flex;
    justify-content: center;
}

.element {
    margin: auto;
    display: flex;
    align-items: flex-start;
    gap:12px;
}

.data {
    font-size: 22px;
    font-weight: 400;
}

.text {
    font-size: 18px;
    font-weight: 400;
}

.icon {
    margin-top: 10px;
}

.logo {
    margin-top: 40px;
    margin-bottom: 40px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    width: 8rem;
    cursor: pointer;
  }
  
  @media (max-width: 768px) {
    .container{
        width: 380px;
        height: 570px;
        margin-left: auto;
        margin-right: auto;
        margin-top: 50px;
    }
    
    .top-bar{
        display: flex;
        justify-content: center;
        gap: 14px;
        padding-top: 38px;
    }
    
    .top-bar input{
        display: flex;
        width: 200px;
        height: 58px;
        background: #ebfffc;
        border: none;
        outline: none;
        border-radius: 40px;
        padding-left: 40px;
        color: #626262;
        font-size: 20px;
        font-weight: 400;
    }
    
    .search-icon{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 58px;
        height: 58px;
        background: #ebfffc;
        border-radius: 40px;
        cursor: pointer;
    }

    .weather-image {
        margin-top: 0px;
      }
    
    .weather-temp {
        display: flex;
        justify-content: center;
        color: white;
        font-size: 52px;
        font-weight: 400;
    }
    
    .weather-location {
        display: flex;
        justify-content: center;
        color: white;
        font-size: 36px;
        font-weight: 400;
    }

    .data-container {
        margin-top: 35px;
        color: white;
        display: flex;
        justify-content: center;
    }
    
    .element {
        margin: auto;
        display: flex;
        align-items: flex-start;
        gap:12px;
    }
    
    .data {
        font-size: 22px;
        font-weight: 400;
    }
    
    .text {
        font-size: 16px;
        font-weight: 400;
    }
    
    .icon {
        margin-top: 10px;
    }

    .logo {
      width: 6rem;
    }
  }